import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams, ViewController } from 'ionic-angular';
import { ToastProvider } from '../../../providers/toast/toast';
import { ClinicsProvider } from '../../../providers/clinics/clinics';
import { ExamSearchPage, SpecializationSearchPage } from '../../index';

/**
 * Generated class for the ClinicDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-clinic-detail',
  templateUrl: 'clinic-detail.html',
})
export class ClinicDetailPage {

  public item: any = {
    id: '',
    contact_name: '',
    exams: '',
    specializations: '',
    name: '', email: '',
    phone_number: '',
    mobile_number: ''
  };

  constructor(public navCtrl: NavController, public navParams: NavParams, private toastPrv: ToastProvider,
              private modalCtrl: ModalController, public viewCtrl : ViewController, private clinicPrv: ClinicsProvider) {
      this.item = this.navParams.get('item') ?  this.navParams.get('item') : this.item;
      console.log('this.item', this.item);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExamDetailPage');
  }

  save() {
    if (!this.isValid()) {
      return;
    }

    let payload = this.item;

    this.clinicPrv.save(payload).subscribe((res) => {
      this.toastPrv.show('Clinica salvo com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      console.log(err);
      this.toastPrv.show('Tivemos problemas em salvar o item!');
    });
  }


  isValid(): boolean {

    if (!this.item.name) {
      this.toastPrv.show('Informe o nome da clínica');
      return false;
    }


    if (!this.item.contact_name) {
      this.toastPrv.show('Informe o nome do contato');
      return false;
    }

    if (!this.item.email) {
      this.toastPrv.show('Informe o e-mail');
      return false;
    }

    if (!this.item.phone_number && !this.item.mobile_number) {
      this.toastPrv.show('Informe ao menos um número de contato');
      return false;
    }

    return true;
  }

  remove() {
    this.clinicPrv.remove(this.item).subscribe((res) => {
      this.toastPrv.show('Item removido com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      this.toastPrv.show('Tivemos problemas em remover o item!');
    });
  }

  dismiss() {
    this.viewCtrl.dismiss({reload: false});
  }

  chooseSpecializations() {
    let specializationSearchModal = this.modalCtrl.create(SpecializationSearchPage, {
      'clinic' : this.item
    });

    specializationSearchModal.onDidDismiss((data: any) => {
      this.item.specializations = data.specializationSelected ? data.specializationSelected : {};
      if (this.item.specializations.length) {
        for (let specialization of this.item.specializations) {
          specialization.price = specialization.price ? specialization.price : 0.0;
        }
      }
    });

    specializationSearchModal.present();
  }

  chooseExams() {
    let examSearchPage = this.modalCtrl.create(ExamSearchPage, {
      'clinic' : this.item
    });

    examSearchPage.onDidDismiss((data: any) => {
      this.item.exams = data.examSelected ? data.examSelected : {};
      if (this.item.exams.length) {
        for (let exam of this.item.exams) {
          exam.price = exam.price ? exam.price : 0.0;
        }
      }
    });

    examSearchPage.present();
  }

}



